/* bitter-400normal - latin */
@font-face {
  font-family: 'Bitter';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Bitter Regular '),
    local('Bitter-Regular'),
    url(/static/media/bitter-latin-400.c536cb9f.woff2) format('woff2'), 
    url(/static/media/bitter-latin-400.d9c3aaf0.woff) format('woff'); /* Modern Browsers */
}

/* bitter-400italic - latin */
